import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Box } from '@mui/material'
import { YoutubeOptions, fetchData, options } from '../utils/fetchData'
import Detail from '../components/Detail'
import ExerciseVideos from '../components/ExerciseVideos'
import SimilarExercises from '../components/SimilarExercises'


const ExerciseDetail = () => {
  const [exerciseDetail, setExerciseDetail] = useState({})
  const [exerciseVideos, setExerciseVideos] = useState([])
  const [targetMuscleExercises, setTargetMuscleExercises] = useState([])
  const [equipmentExercises, setEquipmentExercises] = useState([])
  const { id } = useParams()

  useEffect(() => {
    const fetchExerciseData = async () => {
      const exerciseDbUrl = 'https://exercisedb.p.rapidapi.com'
      const youtubeSearchUrl = 'https://youtube-search-and-download.p.rapidapi.com'

      const exerciseDetailData = await
        fetchData(`${exerciseDbUrl}/exercises/exercise/${id}`, options)
      setExerciseDetail(exerciseDetailData)

      const exerciseVideoData = await
        fetchData(`${youtubeSearchUrl}/search?query=${exerciseDetailData.name}`, YoutubeOptions)
      setExerciseVideos(exerciseVideoData.contents)

      const targetMuscleExercisesData = await
        fetchData(`${exerciseDbUrl}/exercises/target/${exerciseDetailData.target}`, options)
      setTargetMuscleExercises(targetMuscleExercisesData)

      const equipmentExercisesData = await
        fetchData(`${exerciseDbUrl}/exercises/equipment/${exerciseDetailData.equipment}`, options)
      setEquipmentExercises(equipmentExercisesData)
    }

    fetchExerciseData()

  }, [id])

  return (
    <Box>
      <Detail exerciseDetail={exerciseDetail} />
      <ExerciseVideos exerciseVideos={exerciseVideos}
        name={exerciseDetail.name}
      />
      <SimilarExercises
        targetMuscleExercises={targetMuscleExercises}
        equipmentExercises={equipmentExercises}
      />

    </Box>
  )
}

export default ExerciseDetail